.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    padding: 2rem;
    background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
    color: #fff;
    font-family: 'Arial', sans-serif;
}

.title {
    font-size: 2.5rem;
    margin-bottom: 2rem;
    text-align: center;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.round {
    width: 100%;
    max-width: 600px;
    padding: 2rem;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 12px;
    margin-bottom: 2rem;
}

.songInfo {
    margin: 1.5rem 0;
    padding: 1rem;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 8px;
}

.songInfo audio {
    width: 100%;
    margin-top: 1rem;
}

.guessForm {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-top: 1.5rem;
}

.guessInput {
    background: rgba(255, 255, 255, 0.1);
    border: 2px solid rgba(255, 255, 255, 0.2);
    border-radius: 8px;
    color: white;
    font-size: 1.1rem;
    padding: 1rem;
    width: 100%;
    transition: all 0.3s ease;
}

.guessInput::placeholder {
    color: rgba(255, 255, 255, 0.5);
}

.guessInput:focus {
    outline: none;
    border-color: #4a90e2;
    background: rgba(255, 255, 255, 0.15);
}

.submitButton {
    background: #4a90e2;
    border: none;
    border-radius: 8px;
    color: white;
    cursor: pointer;
    font-size: 1.1rem;
    padding: 1rem 2rem;
    transition: all 0.3s ease;
}

.submitButton:hover {
    background: #357abd;
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.waiting,
.roundEnded,
.transition,
.gameEnded {
    text-align: center;
    padding: 2rem;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 12px;
    width: 100%;
    max-width: 600px;
}

.homeButton {
    background: #e94560;
    border: none;
    border-radius: 8px;
    color: white;
    cursor: pointer;
    font-size: 1.1rem;
    padding: 1rem 2rem;
    margin-top: 2rem;
    transition: all 0.3s ease;
}

.homeButton:hover {
    background: #d13652;
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.roundEnded ul,
.gameEnded ul {
    list-style: none;
    padding: 0;
    margin: 1.5rem 0;
}

.roundEnded li,
.gameEnded li {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 8px;
    padding: 0.8rem;
    margin-bottom: 0.5rem;
}



/* Transition card styling */
.transition {
    width: 100%;
    max-width: var(--max-width);
    padding: 2rem;
    background: var(--card-bg);
    border-radius: 12px;
    margin: 0 auto;
    text-align: center;
    animation: fadeIn 0.5s ease-out;
}

.transition h2 {
    font-size: 2rem;
    margin-bottom: 1.5rem;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.transition p {
    font-size: 1.2rem;
    margin: 0.8rem 0;
}

.transition div {
    background: rgba(255, 255, 255, 0.05);
    padding: 1.5rem;
    border-radius: 8px;
    margin: 1rem 0;
}

.resultCard {
    background: rgba(255, 255, 255, 0.05);
    padding: 1.5rem;
    border-radius: 8px;
    margin: 1rem 0;
}

.resultTitle {
    font-size: 1.3rem;
    color: #fff;
    margin-bottom: 1rem;
}

.correctAnswer {
    font-weight: bold;
    color: #4a90e2;
    font-size: 1.2rem;
    margin: 0.8rem 0;
}

.correctGuess {
    color: #4caf50;
    font-weight: bold;
    font-size: 1.2rem;
    margin: 0.8rem 0;
}

.incorrectGuess {
    color: #e94560;
    font-size: 1.2rem;
    margin: 0.8rem 0;
}

.noGuess {
    color: #888;
    font-size: 1.2rem;
    margin: 0.8rem 0;
}

.timer {
    font-size: 1.5rem;
    font-weight: bold;
    color: #4a90e2;
    margin-top: 1.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
}