.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 2rem;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
  color: #fff;
  font-family: 'Arial', sans-serif;
}

.title {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  text-align: center;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.choiceContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 500px;
  }
  
  .joinContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 1.5rem;
    width: 100%;
  }
  
  .button {
    background: #4a90e2;
    border: none;
    border-radius: 8px;
    color: white;
    cursor: pointer;
    font-size: 1.1rem;
    padding: 1rem 2rem;
    transition: all 0.3s ease;
    white-space: nowrap;
    min-width: 150px; /* Set a fixed minimum width for buttons */
    width: 150px;     /* Set a fixed width for buttons */
  }
  
  .createButton {
    width: 100%;
    max-width: 500px;
  }
  
  .button:hover {
    background: #357abd;
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
  
  .input {
    background: rgba(255, 255, 255, 0.1);
    border: 2px solid rgba(255, 255, 255, 0.2);
    border-radius: 8px;
    color: white;
    font-size: 1.1rem;
    padding: 1rem;
    flex: 1;
    transition: all 0.3s ease;
    margin-right: 0.5rem;
  }
  .backButton {
    background: #357abd;
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    /* put the button on top left */
    position: absolute;
    top: 20px;
    left: 20px;
  }