.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 2rem;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
  color: #fff;
  font-family: 'Arial', sans-serif;
}

.title {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  text-align: center;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.songContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  width: 100%;
  max-width: 600px;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  margin-bottom: 2rem;
}

/* audio player styles */
.audioPlayer {
  width: 100%;
  max-width: 600px;
  margin-top: 1rem;
}

.songInfo {
  margin: 1.5rem 0;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  width: 100%;
}

.songInfo audio {
  width: 100%;
  margin-top: 1rem;
}

.input, .guessInput {
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  color: white;
  font-size: 1.1rem;
  padding: 1rem;
  width: 100%;
  max-width: 400px;
  transition: all 0.3s ease;
}

.input::placeholder, .guessInput::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.input:focus, .guessInput:focus {
  outline: none;
  border-color: #4a90e2;
  background: rgba(255, 255, 255, 0.15);
}

.button, .submitButton {
  background: #4a90e2;
  border: none;
  border-radius: 8px;
  color: white;
  cursor: pointer;
  font-size: 1.1rem;
  padding: 1rem 2rem;
  transition: all 0.3s ease;
  margin: 0.5rem;
}

.button:hover, .submitButton:hover {
  background: #357abd;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.message {
  font-size: 1.2rem;
  margin-top: 1rem;
}

.score {
  font-size: 1.75rem;
  margin-top: 2rem;
  margin-bottom:2rem;
}

.go_back_button {
  background: #357abd;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  position: absolute;
  top: 20px;
  left: 20px;
}

.round {
  width: 100%;
  max-width: 600px;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  margin-bottom: 2rem;
}

.guessForm {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 1.5rem;
}


.homeButton {
  background: #e94560;
  border: none;
  border-radius: 8px;
  color: white;
  cursor: pointer;
  font-size: 1.1rem;
  padding: 1rem 2rem;
  margin-top: 2rem;
  transition: all 0.3s ease;
}

.homeButton:hover {
  background: #d13652;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}








@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}
