/* App.css */

/* General resets and basic styling */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f4f4f4;
  color: #333;
  line-height: 1.6;
}

/* Container to center content */
.container {
  max-width: 960px;
  margin: 0 auto;
  padding: 20px;
}

/* Header styling */
header {
  background: #333;
  color: #fff;
  padding: 20px 0;
  text-align: center;
}

/* Navigation */
nav ul {
  list-style: none;
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

nav ul li {
  margin: 0 10px;
}

nav ul li a {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
}

/* Button styling */
button {
  background: #007bff;
  color: #fff;
  border: none;
  padding: 10px 15px;
  margin: 5px;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s ease;
}

button:hover {
  background: #0056b3;
}

/* Form styling */
form {
  margin: 20px 0;
}

input[type="text"],
input[type="number"],
textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

/* Lobby and Game views */
.lobby,
.game {
  background: #fff;
  padding: 20px;
  margin: 20px 0;
  border-radius: 4px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

h1, h2, h3 {
  margin-bottom: 15px;
}

/* Audio player styling */
audio {
  width: 100%;
  margin-top: 10px;
}

/* Message styling */
.message {
  padding: 10px;
  margin-top: 10px;
  border-radius: 4px;
}

.message.success {
  background-color: #d4edda;
  color: #155724;
}

.message.error {
  background-color: #f8d7da;
  color: #721c24;
}
